@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Montserrat:wght@300;400;500;600&display=swap');

:root{
  --primary-font:font-family: 'Lobster', cursive;
  --secondary-font:font-family: 'Montserrat', sans-serif;
}
body{
  font-family:'Montserrat', sans-serif ;
}
.navbar-brand{
  font-size: 2rem;
  font-family:'Lobster', cursive ;
}

.nav-link{
  font-size: 1.1rem;
  font-family:'Montserrat', sans-serif;
  font-weight: 500;
}
h1,h2{
  font-family: 'Lobster', cursive;
}
p{
  font-size: 1rem;
  font-weight: bold;
}

.btn{
  font-size: 1.3rem;
  font-weight: 700;
}
label{
  font-size: 1.1rem;
  font-weight: bold;
}

.alert{
  font-size: 1.2rem;
}